<template>
  <div>
    <mbread :isOpen="true" :breadSecond="breadSecond" :breadname="breadname"></mbread>
    <el-select class="select" v-model="value1" placeholder="请选择" @change="changeCategory">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
      </el-option>
    </el-select>

    <el-table :data="pageData" border width="95%" :stripe="true"   :default-sort = "{prop: 'datetime', order: 'descending'}">
        <el-table-column
          prop="title"
          label="标题" width="400">
        </el-table-column>

        <el-table-column
          prop="category"
          label="类别">
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.status === '1' ? 'success' : 'danger'"
              disable-transitions>{{scope.row.status== '1' ? '已通过':'未通过'}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="datetime"
          label="时间" width="150px">
        </el-table-column>
        <el-table-column
          label="操作" align="center"
          width="150">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="success" circle size="small">详情</el-button>
            <el-button type="danger" size="small" circle>删除</el-button>
          </template>
        </el-table-column>
    </el-table>

    <el-row>
      <el-col :span="24" :xs="10"> .
        <el-pagination
          background
          layout="total,prev, pager, next"
          :total="total" :current-page="1"  @current-change="changePage" >
        </el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import mbread from '@/components/header/mbread'
  import ElPager from "element-ui/packages/pagination/src/pager";
  import axios from 'axios'
    export default {
        name: "report-list",
        data() {
            return {
              breadSecond: '报告',
              breadname:'查看报告',
              value1:'',
              options: [
                {
                  value: '0',
                  label: '现场报告'
                },
                {
                  value: '1',
                  label: '时事报告'
                },
                {
                  value: '2',
                  label: 'AOG报告'
                 },
                {
                  value: '3',
                  label: '飞行报告'
              },{
                value: '4',
                  label: ' 所有'
                }],
              reportData: [],
              pageData: [],
              total: 0,


            }
        },
      components:{
        ElPager,
        mbread
      },
      mounted(){
        this._initReportData();
      },
      methods:{
        /*初始化获取报告数据 */
        _initReportData() {
          axios.get('/reports/getAllreport').then(response=>{
            let res = response.data;
            if (res.status === '0'){
                this.total = res.result.length;
                res.result.forEach(item=>{
                  let data = [];
                  data.title = item.title;
                  data.desc = item.desc;
                  data.status = item.status;
                  data.category = item.category;
                  data.datetime = item.datetime;
                  data.reportId = item.reportId;
                  this.reportData.push(data);
                });
                this._initPage(1);
            }else {
              this.$notify({
                title: '出错了狗子',
                dangerouslyUseHTMLString: true,
                message: '<span style="color: red;">获取报告数据失败! 检查网络或者重试</span>',
                duration: 3000
              })
            }
          } )
        },
        /*切换分页*/
        changePage(index) {
          this._initPage(index);
        },
        _initPage(current) {
          this.pageData = [];
          let total = current* 10;
          let min = (total-10)<=0? 0:total-10;
          for (let i = min;i<total;i++){
            if (this.reportData[i]){
              this.pageData.push(this.reportData[i]);
            }
          }
        },
        /*选择不同分类*/
        changeCategory(value){
          if (value === '4'){
            this.pageData = this.reportData;
            this.total = this.pageData.length;
            return this.pageData;
          }
          this.pageData = this.reportData.filter(item=>{
            return item.status === value;
          })
          this.total = this.pageData.length;
        },
        /* 跳转路由查看报告详情*/
        handleClick(data){
          localStorage.setItem('reportId',data.reportId);
          this.$router.push({name: 'reportCheck',params:{
            reportId: data.reportId
            }})
        },
        /* 点击标题*/
        clickTitle() {
          console.log('1111');
        }
      }
    }
</script>

<style scoped>
  .select{
    margin: 10px 0;
  }
  .el-pagination{
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 40%;
  }
  .OK{
    content: '已通过';
    color: #5daf34;
  }
  .fail{
    content: '未通过';
    color: red;
  }
</style>



